.products {
  padding-top: 120px;
  padding-bottom: 100px;
}

.products__subtitle,
.products__label-title,
.products__title {
  font-family: $title-font-family;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
}

.products__label-title {
  margin-bottom: 15px;
  font-size: getFont-size__mob('label-title');
  line-height: 1.17;

  color: get-titleColor('accent-pale');

  @media screen and (min-width: $tablet) {
    font-size: getFont-size__tab('card-title label-title');
    line-height: 1.15;
  }

  @media screen and (min-width: $desktop) {
    font-size: getFont-size__desk('card-title label-title');
    line-height: 1.13;
  }
}

.products__title {
  margin-bottom: 145px;
  font-size: getFont-size__mob('title-section');
  line-height: 1.15;

  color: get-titleColor('accent-bright');

  @media screen and (min-width: $tablet) {
    font-size: getFont-size__tab('title-section');
    margin-bottom: 150px;
  }

  @media screen and (min-width: $desktop) {
    font-size: getFont-size__desk('title-section');
    margin-bottom: 260px;
  }
}

.products__list {
  color: get-textColor('main-light');
  
  @media screen and (min-width: $tablet) {
    display: flex;
    margin: -10px;
  }
  @media screen and (min-width: $tablet) {
    display: flex;
    margin: -15px;
  }

}

.products__item {
  position: relative;
  border-radius: 24px;
  text-align: center;
  padding: 160px 40px 40px;
  @media screen and (max-width: $max-mobile) {
    &:not(:last-child) {
      margin-bottom: 130px;
    }
  }
  &:nth-child(1) {
    background-color: get-elementColor('card-primary');
  }
  &:nth-child(2) {
    background-color: get-elementColor('card-secondary');
  }
  &:nth-child(3) {
    background-color: get-elementColor('card-tertiary');
  }

  @media screen and (min-width: $tablet) {
    flex-basis: calc((100% - 2 * 20px) / 3);
    margin: 10px;
    padding: 152px 10px 40px;
  }

   @media screen and (min-width: $desktop) {
    flex-basis: calc((100% - 2 * 30px) / 3);
    padding: 163px 60px 50px;
    margin: 15px;
  }
  
}

.products__subtitle {
  margin-bottom: 66px;
  font-size: getFont-size__mob('subtitle');
  line-height: 1.15;

   @media screen and (min-width: $desktop) {
    font-size: getFont-size__desk('card-title label-title');
    margin-bottom: 70px;
  }
}

.products__subtitle--pink::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  @media screen and (max-width: $max-mobile) {
    top: -113px;
    width: 249px;
    height: 252px;
    background-image: url(../../images/products/optimisized__png/mob/icecream__mob.png);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../../images/products/optimisized__png/mob/icecream__mob@2x.png);
    }
    @media screen and (min-device-pixel-ratio: 3),
      screen and (min-resolution: 288dpi),
      screen and (min-resolution: 3dppx) {
      background-image: url(../../images/products/optimisized__png/mob/icecream__mob@3x.png);
    }
  }

  @media screen and (min-width: $tablet) {
    top: -102px;
    width: 220px;
    height: 225px;
    background-image: url(../../images/products/optimisized__png/tablet/icecream__tablet.png);


    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../../images/products/optimisized__png/tablet/icecream__tablet@2x.png);
    }
    @media screen and (min-device-pixel-ratio: 3),
      screen and (min-resolution: 288dpi),
      screen and (min-resolution: 3dppx) {
      background-image: url(../../images/products/optimisized__png/tablet/icecream__tablet@3x.png);
    }
  }

  @media screen and (min-width: $desktop) {
    top: -210px;
    width: 335px;
    height: 335px;
    background-image: url(../../images/products/optimisized__png/desktop/icecream__desktop.png);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../../images/products/optimisized__png/desktop/icecream__desktop@2x.png);
    }
    @media screen and (min-device-pixel-ratio: 3),
      screen and (min-resolution: 288dpi),
      screen and (min-resolution: 3dppx) {
      background-image: url(../../images/products/optimisized__png/desktop/icecream__desktop@3x.png);
    }
  }
}

