.products__subtitle::after {
  content: '';
  position: absolute;
  top: 214px;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  width: 26px;
  height: 6px;
  background-image: url(../../images/svg/elipsis-white.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.products__text {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;

   @media screen and (min-width: $tablet) {
    line-height: 1.76;
    margin-bottom: 40px;
  }

  @media screen and (min-width: $desktop) {
    font-size: getFont-size__desk('text-more');
    margin-bottom: 60px;
  }
}

.products__button-arrow {
  @extend %btn-4;
  width: 40px;
  height: 40px;
}
